/****************************************************/

body {
  font-family: 'ProximaNova_reg', sans-serif;
}

.clearfix:after {
  content: '';
  clear: both;
  display: block;
  height: 0;
  width: 0;
}

ul {
  list-style-type: none;
}

/** ---------------------------------------
 * Slider 
 ----------------------------------------*/

.container {
  width: 100%;
}

.slider-wrapper {
  display: flex;
  z-index: 500;
  position: relative;
  width: 100%;
  height: 100vh;
  background: #FFF;
  overflow: hidden;
}

.slider-wrapper .head__desc {
  position: relative;
  z-index: 999;
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: center;
  flex-direction: column;
}

.slider-wrapper .head__desc h1 {
  font-size: 56px;
  color: #FFF;
  margin-bottom: 10px;
  font-weight: bold;
  font-family: 'ProximaNova_reg', sans-serif;
}

.slider-wrapper .head__desc h2 {
  font-size: 24px;
  color: #FFF;
  margin-bottom: 84px;
  font-family: 'ProximaNova_reg', sans-serif;
}

.slider-wrapper li {
  display: none;
}

.slider-wrapper .current-slide {
  display: block;
}

.slider-shadow {
  width: 100%;
  height: 15px;
  position: relative;
}

.slider-shadow:after,
.slider-shadow:before {
  content: '';
  position: absolute;
  background: #171c24;
  height: 100%;
  width: 50%;
  left: 10px;
  top: -20px;
  -webkit-transform: rotate(-4deg);
  -ms-transform: rotate(-4deg);
  -o-transform: rotate(-4deg);
  transform: rotate(-4deg);
  -webkit-box-shadow: 0 0 15px 8px #171c24;
  box-shadow: 0 0 15px 8px #171c24;
}

.slider-shadow:before {
  right: 10px;
  left: auto;
  -webkit-transform: rotate(4deg);
  -ms-transform: rotate(4deg);
  -o-transform: rotate(4deg);
  transform: rotate(4deg);
}

.slider-wrapper img {
  position: absolute;
  width: 100%;
  height: auto;
  top: 0;
  left: 0;
}

/**
 * ---[Caption] ---------------------- 
 **/

.slider-wrapper .caption {
  position: absolute;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.65);
  width: 100%;
  padding: 10px;
  color: #FFF;
  -webkit-transform: translateY(100%);
  -ms-transform: translateY(100%);
  -o-transform: translateY(100%);
  transform: translateY(100%);
  opacity: 0;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

.slider-wrapper li:hover .caption {
  -webkit-transform: translateY(0);
  -ms-transform: translateY(0);
  -o-transform: translateY(0);
  transform: translateY(0);
  opacity: 1;
}

.slider-wrapper h2 {
  color: #00c5b9;
  font-size: 2em;
  font-weight: 400;
  margin-bottom: 6px;
}

.slider-wrapper p {
  font-size: 1.6em;
  font-weight: 300;
  line-height: 1.4em;
}

/**
 * ---[Botones-Control] ---------------------- 
 **/

.control-buttons {
  position: absolute;
  width: 100%;
  margin-bottom: 95px;
  bottom: 0;
  text-align: center;
  z-index: 999;
}

.control-buttons li {
  cursor: pointer;
  display: inline-block;
  text-indent: -99999px;
  height: 10px;
  width: 10px;
  margin: 0 2px;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  border-radius: 50%;
  border: 2px solid #dadada;
}

.control-buttons li.active {
  background: #fff;
}

.authors {
  text-align: center;
  color: #7a8699;
  display: block;
  font-size: 1.6em;
  font-weight: 300;
  margin-top: 80px;
  font-size: 300;
}

/** ---------------------------------------
 * Responsive 
 ----------------------------------------*/

.mouse {
  position: relative;
  display: flex;
  /* top: auto; */
  bottom: 89px;
  margin: 0 auto;
  z-index: 999;
  width: 19px !important;
  height: 79px;
}

.navbar {
  position: fixed;
  z-index: 999;
  min-height: 75px;
  border-bottom: 1px solid #7a8699;
  width: 100%;
}

.navbar .row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  min-height: 75px;
}

.navbar .row .menu__item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}

.navbar .row .menu__item li,
.navbar .row .menu__item a {
  text-decoration: none;
  justify-content: space-between;
  padding: 10px 10px;
  color: #FFF;
  font-size: 14px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.navbar .row .menu__item a:hover {
  color: #fbb02c;
}

.navbar .row__right {
  font-size: 14px;
  font-weight: bold;
  color: #FFF;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

button {
  color: #000;
  background: #fbb02c;
  padding: 8px 0;
  border-radius: 3px;
  text-align: center;
  border-style: none;
  height: 36px;
  min-width: 165px;
}

.head__form {
  display: flex;
  justify-content: space-between;
  width: 50%;
}

.head__form input {
  border: 0;
  border-bottom: #FFF 1px solid;
  background: none;
  height: 28px;
  width: 360px;
  outline: none;
}

